.order-popup {
    position: fixed;
    width: 90vw;
    padding: 46px 15px 20px 15px;
    background-color: red;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background: #151515;
    border-radius: 12px;
    top: -9999px;
    z-index: 5;
    transition: all .5s ease;

    @include desktop {
        width: 551px;
        padding: 60px 65px 70px 65px;
    }
    
    input[type="text"] {
        width: 100%;
        background: #151515;
        border: 1px solid #fff;
        border-radius: 12px;
        height: 40px;
        padding: 18px 25px;
        font-size: 12px;
        font-weight: 400;
        color: #4A4A4A;
        margin-bottom: 30px;
        @include desktop{
            height: 60px;
            font-size: 18px;
        }
    }

    input[type="radio"], input[type="checkbox"] {
        appearance: none;
        display: block;
        position: relative;
        width: 14px;
        height: 14px;
        border: 1px solid #fff;
        border-radius: 4px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        &::after {
            content: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='7.50049' cy='1.42896' r='1.01008' transform='rotate(45 7.50049 1.42896)' fill='white'/%3E%3Ccircle cx='8.57178' cy='2.50025' r='1.01008' transform='rotate(45 8.57178 2.50025)' fill='white'/%3E%3Ccircle cx='1.4292' cy='7.50025' r='1.01008' transform='rotate(45 1.4292 7.50025)' fill='white'/%3E%3Ccircle cx='2.50049' cy='8.57203' r='1.01008' transform='rotate(45 2.50049 8.57203)' fill='white'/%3E%3Crect x='7.26611' y='1.90869' width='1.16801' height='7.57563' transform='rotate(45 7.26611 1.90869)' fill='white'/%3E%3Ccircle cx='1.42945' cy='2.50049' r='1.01008' transform='rotate(-45 1.42945 2.50049)' fill='white'/%3E%3Ccircle cx='2.50074' cy='1.4292' r='1.01008' transform='rotate(-45 2.50074 1.4292)' fill='white'/%3E%3Ccircle cx='7.50074' cy='8.57129' r='1.01008' transform='rotate(-45 7.50074 8.57129)' fill='white'/%3E%3Ccircle cx='8.57203' cy='7.5' r='1.01008' transform='rotate(-45 8.57203 7.5)' fill='white'/%3E%3Crect x='1.90967' y='2.73486' width='1.16801' height='7.57563' transform='rotate(-45 1.90967 2.73486)' fill='white'/%3E%3C/svg%3E%0A");
            display: block;
            margin-top: -5px;
            width: 10px;
            height: 10px;
            position: absolute;
            opacity: 0;
            transition: all .2s ease;
        }
        &:checked {
            &:after {
                opacity: 1;
            }
        }
        @include desktop {
            width: 16px;
            height: 16px;
        }
    }

    .order__title {
        margin-bottom: 40px;
        font-size: 24px;
        @include desktop {
            margin-bottom: 70px;
        }
    }
    .order__contact-method {
        margin-bottom: 25px;
        @include desktop {
            margin-bottom: 30px;
        }
    }
    .order__contact-descr {
        margin-bottom: 25px;
        font-size: 14px;
        display: block;
        @include desktop {
            margin-bottom: 30px;
            font-size: 16px;
        }
    }
    
    .order__radio-holder {
        display: flex;
        align-items: center;
        input {
            margin: 1px;
            margin-left: 15px;
            @include desktop {
                margin-left: 35px;

            }
        }
        label {
            margin-left: 10px;
            font-size: 11px;
            font-weight: 400;
            @include desktop {
                font-size: 16px;
            }
        }
        & :first-child {
            margin-left: 0;
        }
    }
    .policy {
        display: flex;
        align-items: center;
        font-size: 10px;
        font-weight: 400;
        margin-bottom: 50px;
        @include desktop {
            font-size: 18px;
        }
    }
    .policy__terms {
        margin-left: 7px;
        display: block;
        @include desktop {
            margin-left: 10px;
        }
    }
    .policy__link {
        text-decoration: underline;
        margin-left: 4px;
    }
    .order__submit {
        padding: 11px 78px;
        border: none;
        background: rgba(255, 255, 255, 0.4);
        border-radius: 12px;
        color: #fff;
    }
}
.order-popup_active {
    top: 50%;
}
