.breadcrumbs {
    display: flex;
    font-family: $font-family;
    @include fontSize(14px, 300);
    margin-bottom: 7px;
    margin-left: 20px;
    @include desktop {
        margin-left: 0;
        margin-bottom: 30px;
    }


    li+li {
        margin-left: 5px;

        &::before {
            content: '/';
        }

        span {
            margin-left: 5px;
        }
    }

    .active {
        color: #606C38;
    }

    &__item {
        color: #F1F1F1;
    }
    &__item_active{
        color: #fff;
    }
}