body {
    background: url('images/background.png') 0% 18% no-repeat;
    background-color: #151515;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    font-family: 'Merriweather' ,'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    @include desktop {
        background: url("images/background2.png") -47% 31% no-repeat;
        
        background-color: #151515;
    }
}
.body-overlay {
    width: 100%;
    height: 100%;
    background: #000;
    position: fixed;
    z-index: -1;
    opacity: 0;
    transition: all .5s ease;
}
.body-overlay_active {
    overflow: hidden;
    .body-overlay {
        z-index: 2;
        opacity: .3;
    }
}
a {
    text-decoration: none;
}
h1, h2, h3, p {
    margin: 0;
}
ul, li, a {
    color: inherit;
    font-size: inherit;
    font-weight: inherit;
}
.main {
    overflow: hidden;
    @include desktop {
        overflow: visible;

    }
}
.container {
    margin: 0 auto;
    max-width: 310px;
    @include desktop {
        max-width: 1280px;
    }
}
.banner {
    display: flex;
    position: relative;
    margin-bottom: 48px;
    @include desktop {
        margin-top: -35px;
        margin-bottom: 120px;
    }
}
.banner__img {
    position: absolute;
    right: -50%;
    width: 320px;
    height: 380px;
    z-index: -1;
    top: -13px;
    @include desktop {
        position: static;
        width: 612px;
        height: 730px;
    }
}
.banner__title {
    max-width: 275px;
    font-size: 38px;
    margin-top: 0;
    margin-bottom: 15px;
    @include desktop {
        max-width: 705px;
        font-size: 96px;
        font-weight: 700;
        margin-bottom: 25px;
        margin-top: 157px;
    }
}
.banner__descr {
    max-width: 264px;
    font-size: 18px;
    margin-bottom: 35px;
    @include desktop {
        margin-top: 0;
        max-width: 470px;
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 50px;
    }
}
.banner__btn {
    font-size: 16px;
    display: inline-block;
    padding: 15px 53px;
    border: 2px solid #fff;
    border-radius: 12px;
    @include desktop {
        padding: 20px 60px;
        font-size: 18px;
    }
}

.products__title {
    font-size: 28px;
    margin: 0;
    margin-bottom: 20px;
    @include desktop {
        font-size: 68px;
        margin-bottom: 70px;
    }
}

//CARD


.products__holder {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-bottom: 70px;
    @include desktop {
        margin-left: -12px;
        margin-bottom: 150px;
    }
}


.card {
    padding: 10px;
    padding-bottom: 15px;
    border: 2px solid #4A4A4A;
    border-radius: 20px;
    width: calc(50% - 10px);
    position: relative;
    margin-left: 10px;
    margin-bottom: 10px;

    //temp 
    @include desktop {
        width: 310px;
        margin-left: 12px;
        margin-bottom: 15px;


    }
}

.card__thumb {
    border-radius: 12px;
    margin-bottom: 15px;
}

.card__title {
    font-size: 12px;
    text-transform: uppercase;
    margin-bottom: 7px;
    display: block;
    @include desktop {
        font-size: 18px;
        margin-bottom: 11px;
    }
}


.card__sticker {
    position: absolute;
    top: 15px;
    right: 17px;
    width: 15px;
    @include desktop {
        right: 20px;
        width: 15px;    
        width: 26px;
    }
}

.card__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card__price {
    font-size: 14px;
    font-weight: 400;
    @include desktop {
        font-size: 28px;
    }
}

.card__buy-btn {
    cursor: pointer;
    font-size: 12px;
    font-weight: 400;
    padding: 7px 33px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    color: #fff;
    //
    box-shadow: none;
    outline: none;
    border: none;
    @include desktop {
        font-size: 18px;
        padding: 11px 55px;
    }
}