$xs-phone: 320px;
$phone: 480px;
$tablet: 768px;
$desktop: 1280px; // 1280
$widescreen: 1440px;


@mixin phone {
    @media screen and (min-width: #{$phone}) {
        @content;
    }
}



@mixin xs-phone {
    @media screen and (min-width: #{$xs-phone}) {
        @content;
    }
}

@mixin tablet {
    @media screen and (min-width: #{$tablet}) {
        @content;
    }
}

@mixin desktop {

    @media screen and (min-width: #{$desktop}) {
        @content;
    }
}

@mixin widescreen {
    @media screen and (min-width: #{$widescreen}) {
        @content;
    }
}