@font-face {
  font-family: 'Merriweather';
  src: local('Merriweather Regular'), local('Merriweather-Regular'), url('fonts/merriweatherregular.woff2') format('woff2'), url('merriweatherregular.woff') format('woff'), url('merriweatherregular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Merriweather';
  src: local('Merriweather Bold'), local('Merriweather-Bold'), url('fonts/merriweatherbold.woff2') format('woff2'), url('merriweatherbold.woff') format('woff'), url('merriweatherbold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}
