.card-popup {
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 320px;
    height: 98vh;
    overflow-y: scroll;
    //top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    background: #151515;
    padding: 46px 15px 20px 15px;
    border-radius: 12px;
    top: -9999px;
    transition: top .5s ease;

    @include desktop {
        flex-direction: row;
        width: 796px;
        padding: 70px 35px;
        height: auto;
        overflow: visible;
        transform: translateX(-50%) translateY(-50%);
    }
}
    .card-popup_active {
    top: 50%;
    opacity: 1;
    z-index: 2;
}


.card-popup__close-icon {
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 15px;
    @include desktop {
        position: absolute;
        right: 30px;
        top: 30px;
    }
}
.card-popup__col:first-of-type {
    width: 100%;
    @include desktop {
        width: 290px;
    }
}
.card-popup__col:last-of-type {
    margin-left: 0px;
    @include desktop {
        margin-left: 23px;
    }
}
.card-popup__img {
    width: 100%;
    border-radius: 12px;
    margin-bottom: 18px;
    display: block;
    @include desktop {
        width: 290px;
    }
}
.card-popup__descr {
    font-size: 12px;
    line-height: 20px;
}
.card-popup__descr:first-of-type {
    margin-bottom: 20px;
}

.card-popup__title {
    font-size: 20px;
    margin-bottom: 30px;
    @include desktop {
        font-size: 24px;
        margin-bottom: 35px;
    }
}
.card-popup__top {
    justify-content: space-between;
    margin-bottom: 55px;
    .card__price {
        font-size: 24px;
        @include desktop {
            font-size: 28px;
        }
    }
    .card__buy-btn {
        padding: 9px 48px;
        font-size: 12px;
        @include desktop {
            padding: 11px 55px;
            font-size: 18px;
        }
    }
    @include desktop {
        margin-bottom: 100px;
        &>:last-child {
            margin-left: 36px;
        }
        justify-content: flex-start;
    }
}
.card-popup__scpec {
    font-size: 14px;
    font-weight: 400;
}
.card-popup__scpec-row {
    .card-popup__scpec-title {
        font-weight: 700;
    }
    .card-popup__scpec-info {
        margin-left: 21px;
    }
    .card-popup__scpec-info:first-of-type {
        margin-left: 10px;
    }
    display: flex;
    margin-bottom: 26px;
}

.card-popup__scpec-row_flowering {
    align-items: center;
    .card-popup__scpec-info {
        padding: 2px 20px;
        background: #6B6B6B;
        margin-left: 0;
        border-radius: 12px;
        position: relative;
        font-size: 8px;
        white-space: nowrap;
        @include desktop {
            font-size: 14px;
            padding: 2px 22px;
        }
    }

    & :nth-child(2) {
        margin-left: 0px;
        margin-right: -14px;
        @include desktop {
            margin-left: 20px;
        }
    }
    & :nth-child(3) {
        background: #2C2C2C;
        margin-right: -16px;
        z-index: -1;
    }
    & :nth-child(4) {
        z-index: -2;
    } 
}
.card-popup__scpec-row_genotype {
    .card-popup__item {
        position: relative;
    }
    .card-popup__item:first-of-type {
        margin-left: 20px;
    }
    .card-popup__scpec-name {
        position: absolute;
        top: -22px;
        font-size: 8px;
        @include desktop {
            font-size: 14px;
        }
    }
    .card-popup__scpec-info {
        padding: 3px 18px;
        background: #6B6B6B;
        margin-left: 0;
        border-radius: 12px;
        position: relative;
        min-width: 69px;
        display: block;
        text-align: center;
        font-size: 8px;
        @include desktop {
            font-size: 14px;
            padding: 2px 22px;
            width: 100px;
        }
    }
    .card-popup__scpec-info_bar-second {
        margin-right: -14px;
        background: #2C2C2C;
    }
    .card-popup__scpec-info_bar-first {
        margin-right: -14px;
    }
    .card-popup__scpec-name_center {
        left: 50%;
        transform: translateX(-50%);
    }
}
.card-popup__bar-top {
    background: transparent;
}


.card-popup__scpec-name_last {
    margin-left: 10px;
}
.card-popup__scpec-row-height {
    .card-popup__bar {
        margin-left: 44px;
    }
    .card-popup__scpec-info {
        font-size: 8px;
        @include desktop {
            font-size: 14px;
        }
    }
    
    .card-popup__bar-top {
        margin-bottom: 5px;
        .card-popup__scpec-info {
            margin-left: 15px;
            @include desktop {
                margin-left: 22px;
            }
        }
        .card-popup__scpec-info:first-of-type {
            margin-left: 0px;
        }
    }

    .card-popup__bar-bottom {
        width: 100%;
        height: 8px;
        background: #2C2C2C;
        border-radius: 12px;
        position: relative;
        overflow: hidden;
        &::after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            height: 100%;
            width: 0;
            border-radius: 12px;
            background: #6B6B6B;
        }
        @include desktop {
            height: 18px;
        }
    }
    .bar-bottom-20 {
        &::after {
            width: 10%;
        }
    }
    .bar-bottom-40 {
        &::after {
            width: 32%;
        }
    }
    .bar-bottom-60 {
        &::after {
            width: 55%;
        }
    }
    .bar-bottom-80 {
        &::after {
            width: 76%;
        }
    }
    .bar-bottom-100 {
        &::after {
            width: 100%;
        }
    }
}
