  font-weight: normal;
  src: local("Roboto Thin"), url("fonts/Roboto-Thin.woff") format("woff"); }

.nav-links,
.navigation,
.navigation-comments, .navigation-single {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 20px; }

.nav-links .next,
.nav-links .prev,
.navigation .next,
.navigation .prev,
.navigation-comments .next,
.navigation-comments .prev, .navigation-single .next a,
.navigation-single .prev a {
  background: #999;
  color: #1f1f1f;
  border: 1px solid transparent;
  padding: 10px 20px;
  font-size: 14px;
  line-height: 18px; }
  .nav-links .next:hover,
  .nav-links .prev:hover,
  .navigation .next:hover,
  .navigation .prev:hover,
  .navigation-comments .next:hover,
  .navigation-comments .prev:hover, .navigation-single .next a:hover,
  .navigation-single .prev a:hover {
    background: #1f1f1f;
    color: #fff; }

@media (min-width: 768px) {
  .content-column {
    display: flex; } }

@media (min-width: 768px) {
  #content {
    width: 70%; } }

#content:only-child {
  width: 100%; }

@media (min-width: 768px) {
  #sidebar {
    width: 30%;
    padding-left: 30px; } }

.widget {
  margin-bottom: 20px; }
  .widget select {
    min-width: 150px;
    width: 100%; }

.search-form,
.post-password-form {
  margin-bottom: 10px; }
  .search-form label,
  .post-password-form label {
    display: block; }
  .search-form input,
  .post-password-form input {
    float: left;
    margin-right: 5px; }
    @media (min-width: 768px) {
      #sidebar .search-form input, #sidebar
      .post-password-form input {
        width: 100%;
        margin: 0 0 5px; } }
    .search-form input[type='submit'],
    .post-password-form input[type='submit'] {
      margin: 0; }

.nav-links,
.navigation,
.navigation-comments {
  position: relative; }
  .nav-links .screen-reader-text,
  .navigation .screen-reader-text,
  .navigation-comments .screen-reader-text {
    position: absolute;
    left: -99999px; }
  .nav-links .page-numbers,
  .navigation .page-numbers,
  .navigation-comments .page-numbers {
    margin: 0 3px; }
    .nav-links .page-numbers:first-child,
    .navigation .page-numbers:first-child,
    .navigation-comments .page-numbers:first-child {
      margin-left: 0; }
    .nav-links .page-numbers:last-child,
    .navigation .page-numbers:last-child,
    .navigation-comments .page-numbers:last-child {
      margin-right: 0; }
  .nav-links .next,
  .nav-links .prev,
  .navigation .next,
  .navigation .prev,
  .navigation-comments .next,
  .navigation-comments .prev {
    transition: background 0.3s ease-in-out;
    max-width: 49%; }
    @media (max-width: 767px) {
      .nav-links .next,
      .nav-links .prev,
      .navigation .next,
      .navigation .prev,
      .navigation-comments .next,
      .navigation-comments .prev {
        font-size: 0; }
        .nav-links .next:after,
        .nav-links .prev:after,
        .navigation .next:after,
        .navigation .prev:after,
        .navigation-comments .next:after,
        .navigation-comments .prev:after {
          content: '»';
          font-size: 14px; } }
  @media (max-width: 767px) {
    .nav-links .prev:after,
    .navigation .prev:after,
    .navigation-comments .prev:after {
      content: '«'; } }

.navigation-single {
  width: 100%; }
  .navigation-single .next,
  .navigation-single .prev {
    max-width: 49%;
    display: flex;
    align-items: center; }
    .navigation-single .next a,
    .navigation-single .prev a {
      transition: background 0.3s ease-in-out;
      display: block; }
      @media (max-width: 767px) {
        .navigation-single .next a,
        .navigation-single .prev a {
          font-size: 0; }
          .navigation-single .next a:after,
          .navigation-single .prev a:after {
            content: '»';
            font-size: 14px; } }
  @media (max-width: 767px) {
    .navigation-single .prev a:after {
      content: '«'; } }

.navigation-single {
  justify-content: space-between;
  align-items: stretch; }
  .navigation-single .next,
  .navigation-single .prev {
