@import 'base/media';
$font-body-large: 16px;
$font-body-small: 14px;
$main-font-color: #fff;
$input-color: rgba(0, 0, 0, 0.4);
$list-color: #000;

//Buttons
$btn-normal: rgba(0, 0, 0, 0.5);
$btn-hovered: rgba(0, 0, 0, 0.6);


$font-family: 'Oswald',
Arial,
Helvetica,
sans-serif;

$dropdown-arrow: "data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L5 5L9 1' stroke='black'/%3E%3C/svg%3E%0A";

%nav-arrow {
  &::after {
    content: url("data:image/svg+xml,%3Csvg width='8' height='14' viewBox='0 0 8 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L7 7L1 13' stroke='%23606C38' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");

    @include desktop {
      display: none;
    }
  }
}

%dropdown-arrow {
  display: flex;
  align-items: center;

  &::after {
    content: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L5 5L9 1' stroke='black'/%3E%3C/svg%3E%0A");
    display: inline-block;
    margin-left: 8px;
    transform: translateY(-2px);
  }

  @include desktop {
  }
}
%catalog-arrow {
  display: flex;
  align-items: center;

  &::after {
    content: url("data:image/svg+xml,%3Csvg width='14' height='9' viewBox='0 0 14 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L7 7L13 1' stroke='%23606C38' stroke-width='2'/%3E%3C/svg%3E%0A");
    display: inline-block;
    margin-left: 8px;
    transform: translateY(-2px);
    width: 12px;
    transition: all .7s ease;
  }

  @include desktop {
  }
}

%svg {
  display: inline-block;
  height: 100%;
}

%counter {
  display: inline-block;
  padding: 0 5px;
  border-radius: 2px;
  background-color: #73996C;
}

%counter-mobile {
  display: inline-block;
  padding: 0 3px;
  border-radius: 2px;
  background-color: #73996C;
}


%flexcenter {
  display: flex;
  align-items: center;
}

@mixin headerBorder($side, $width) {
  border-#{$side}: $width solid rgba(237, 230, 226, 0.4);
}

@mixin fontSize ($font-size, $font-weight) {
  font-size: $font-size;
  font-weight: $font-weight;
  font-family: $font-family;
}

@mixin colorTransition ($color) {
  transition: background-color .7s ease;
  &:hover {
    background-color: $color;
    transition: background-color .7s ease;
  }
}

// list reset
%listreset {
  margin: 0;
  padding: 0;
  list-style: none;
}


// clearfix
%clearfix {
  &:after {
    content: '';
    display: block;
    clear: both;
  }
}

.clearfix {
  @extend %clearfix;
}

// transition
%transition {
  transition: all 0.3s ease-in-out;
}

// justify nav
%justify {
  text-align: justify;
  font-size: 1px;
  line-height: 0;

  >* {
    display: inline-block;
    vertical-align: top;
    text-align: left;
    font-size: $font-size-base;
    line-height: $line-height-base;
  }

  &:after {
    content: '';
    width: 100%;
    display: inline-block;
    vertical-align: top;
  }
}

%bgCover {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

%absoluteCover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/// * Helper class to truncate and add ellipsis to a string too long for it to fit
/// * on a single line.
/// * 1. Prevent content from wrapping, forcing it on a single line.
/// * 2. Add ellipsis at the end of the line.

.ellipsis {
  white-space: nowrap;
  /* 1 */
  text-overflow: ellipsis;
  /* 2 */
  overflow: hidden;
}

.df-row {
  @include add-params();
  @include flex-grid();

  display: flex;
  flex-wrap: wrap;

  @include media('≥tablet') {
    margin: 0 (-$gutter);
  }
}

